import React from 'react';
import { Link } from 'react-router-dom';
import slugify from 'slugify';

function BoxImage({ listBox, idContent }) {
  const scrollToTop = () => {
    window.scrollTo(0, 0);
  };

  return (
    <div className="grid">
      <div className="bg-gray-200">
        <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
          <div className="mx-auto max-w-3xl py-8 sm:py-18 lg:max-w-none">
            <h2 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl mb-8">
              {listBox[idContent] ? listBox[idContent].title : 'Danh mc'}
            </h2>

            <div className="mt-6 sm:grid sm:grid-cols-2 sm:gap-x-4 lg:grid lg:grid-cols-3 lg:gap-x-6">
              {listBox.map((list, index) => {
                const slug = list.main
                  ? slugify(list.title, { lower: true, replacement: '-' })
                  : slugify(list.name, { lower: true, replacement: '-' });
                return (
                  <div className="group relative mb-8" key={index}>
                    <div className="flex justify-center relative h-[300px] w-full overflow-hidden rounded-lg bg-white group-hover:opacity-75 duration-200">
                      <img
                        src={list.srcImageMain || `https://i.ytimg.com/vi/${list.srcYtb.split('?')[0]}/hqdefault.jpg`}
                        alt={list.main ? list.title : list.name}
                        className="h-full"
                      />
                    </div>
                    <h3 className="mt-1 text-sm text-gray-500">
                      <Link
                        onClick={scrollToTop}
                        to={
                          list.main
                            ? `/content/${index + 1}/${slug}`
                            : `/detail/${idContent}/${index + 1}/${slug}`
                        }
                      >
                        <span className="absolute inset-0"></span>
                        {list.main ? '' : list.title}
                      </Link>
                    </h3>
                    <p className="text-lg font-semibold text-gray-900">
                      {list.main ? list.title : list.name}
                    </p>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BoxImage;
