import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Navigate } from 'react-router-dom';
import { initializeApp } from 'firebase/app';
import { getDatabase, ref, get } from 'firebase/database';

import { publicRoutes, privateRoutes } from '~/routes';
import Context from './Context';
import './App.css';
import dataLocal from '~/data/data';

const firebaseConfig = {
    apiKey: process.env.FIREBASE_API_KEY,
    authDomain: process.env.FIREBASE_AUTH_DOMAIN,
    databaseURL: process.env.FIREBASE_DATABASE_URL,
    projectId: 'thuvienhoituong',
    storageBucket: 'thuvienhoituong.appspot.com',
    messagingSenderId: '180158659637',
    appId: '1:180158659637:web:ad69e93258aa5ae41e857c',
    measurementId: 'G-BP2GRHWQWN',
};

initializeApp(firebaseConfig);

const isAuthenticated = false; // Replace this with your actual authentication logic

const RedirectComponent = () => {
    return <Navigate to="/login" />;
};

function App() {
    const [data, setData] = useState(dataLocal);
    useEffect(() => {
        const database = getDatabase();
        const dataRef = ref(database, 'category');
        get(dataRef)
            .then((snapshot) => {
                const category = snapshot.val();
                setData(category);
            })
            .catch((error) => {
                console.error(error);
            });

        return () => {
            // M lnh thc thi khi component b unmount hoc cc dependency thay i trc ln render tip theo
        };
    }, []);

    return (
        <Context.Provider value={data}>
            <Router>
                    <Routes>
                        {publicRoutes.map((route, index) => {
                            const Page = route.component;
                            return <Route key={index} path={route.path} element={<Page />} />;
                        })}

                        {privateRoutes.map((route, index) => {
                            const Page = route.component;
                            return isAuthenticated ? (
                                <Route key={index} path={route.path} element={<Page />} />
                            ) : (
                                <Route key={index} path={route.path} element={<RedirectComponent />} />
                            );
                        })}
                    </Routes>
            </Router>
        </Context.Provider>
    );
}

export default App;
