import React, { useContext, useRef } from 'react';
import { useParams } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import slugify from 'slugify';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons';

import DefaultLayout from '~/components/Layouts/DefaultLayout';
import Button from '~/components/Button';
import Context from '~/Context';
import LoadingComponent from '~/components/LoadingComponent';

function Detail() {
    const { idContentParam, idDetailParam } = useParams();
    const listBox = useContext(Context);
    let isLoading = true;
    let idContent = 1;
    let idDetail = 1;
    if (listBox.length > 1) {
        isLoading = false;
        idContent = idContentParam;
        idDetail = idDetailParam;
    }

    const list = listBox[idContent - 1].item[idDetail - 1];
    let desc = list.description.split('<br>');
    const listPrev = listBox[idContent - 1].item[idDetail - 2];
    const listNext = listBox[idContent - 1].item[idDetail];
    const slugPrev = listPrev ? slugify(listPrev.name, { lower: true, replacement: '-' }) : '';
    const slugNext = listNext ? slugify(listNext.name, { lower: true, replacement: '-' }) : '';

    const targetRef = useRef(null);

    const handleClick = () => {
        targetRef.current.scrollIntoView({ behavior: 'smooth' });
    };

    return (
        <DefaultLayout>
            {isLoading ? (
                <LoadingComponent />
            ) : (
                <>
                    <Helmet>
                        <title>{list.name} - Th vin hi tng</title>
                    </Helmet>
                    <div ref={targetRef} className="content mb-12 bg-gray-100 px-4 sm:px-6 lg:px-20 py-8 sm:py-18">
                        <div className="mx-auto grid max-w-2xl grid-cols-1 items-center gap-x-8 gap-y-16 lg:max-w-7xl lg:grid-cols-2">
                            <h2 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl mb-8">
                                Danh mc: {list.title}
                            </h2>
                        </div>
                        <div className="mb-12">
                            <div
                                className={`mx-auto grid max-w-2xl grid-cols-1 items-center gap-x-8 gap-y-16 lg:max-w-7xl  ${
                                    list.srcYtb ? 'lg:grid-cols-1' : 'lg:grid-cols-1'
                                }`}
                            >
                                <div className="h-full w-full grid gap-4 sm:gap-6 lg:gap-8">
                                    {list.srcImageMain ? (
                                        <div
                                            className={`grid items-center ${
                                                list.srcImageMain.length >= 2 ? 'grid-cols-1 sm:grid-cols-2' : ''
                                            }`}
                                        >
                                            {list.srcImageMain.map((img, index) => {
                                                return (
                                                    <img
                                                        src={img}
                                                        alt={list.name}
                                                        className="max-h-[400px] sm:max-h-[600px] mx-auto rounded-lg bg-gray-100 p-4"
                                                    />
                                                );
                                            })}
                                        </div>
                                    ) : (
                                        <iframe
                                            className="w-full lg:w-[80%] lg:min-h-[560px] sm:min-h-[400px] min-h-[300px] mx-auto rounded-lg bg-gray-100"
                                            src={`https://www.youtube.com/embed/${list.srcYtb}`}
                                            title="Du Chn K Nim - Tun V (Tc gi: Mnh Pht, Thanh Phng) | Nhc Vng Xa Bt H"
                                            frameBorder="0"
                                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                                            allowFullScreen={true}
                                        ></iframe>
                                    )}
                                </div>
                                <div>
                                    <h2 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl mb-4">
                                        {list.name}
                                    </h2>
                                    {desc.map((item, index) => {
                                        return (
                                            <p key={index} className="text-gray-500 text-justify">
                                                {item}
                                            </p>
                                        );
                                    })}
                                </div>
                            </div>
                        </div>
                        <div className="mx-auto flex align-center justify-center">
                            {listPrev ? (
                                <Button onClick={handleClick} url={`/detail/${idContent}/${listPrev.id}/${slugPrev}`}>
                                    <FontAwesomeIcon className="mr-2" icon={faAngleLeft} />
                                    Trang trc
                                </Button>
                            ) : (
                                <Button disabled={true}>
                                    <FontAwesomeIcon className="mr-2" icon={faAngleLeft} />
                                    Trang trc
                                </Button>
                            )}
                            {listNext ? (
                                <Button onClick={handleClick} url={`/detail/${idContent}/${listNext.id}/${slugNext}`}>
                                    Trang sau
                                    <FontAwesomeIcon className="ml-2" icon={faAngleRight} />
                                </Button>
                            ) : (
                                <Button disabled={true}>
                                    Trang sau
                                    <FontAwesomeIcon className="ml-2" icon={faAngleRight} />
                                </Button>
                            )}
                        </div>
                    </div>
                </>
            )}
        </DefaultLayout>
    );
}

export default Detail;
