import React, { useContext } from 'react';
import { Helmet } from 'react-helmet';

import DefaultLayout from '~/components/Layouts/DefaultLayout';
import LoadingComponent from '~/components/LoadingComponent';
import BoxImage from '~/components/BoxImage';
import Context from '~/Context';

function Home() {
    const listBox = useContext(Context);
    let isLoading = true;
    if(listBox.length > 1) {
        isLoading = false;
    }
    return (
        <DefaultLayout home={true}>
            <Helmet>
                <title>Trang ch - Th vin hi tng</title>
            </Helmet>
            <div className="content">
                <div className="VR-img mb-12">
                    <a href="/virtualtour">
                        <div className="group relative">
                            <div className="relative h-80 w-full overflow-hidden bg-white group-hover:opacity-75 duration-200y">
                                <img
                                    src="https://tailwindui.com/img/ecommerce-images/home-page-02-edition-01.jpg"
                                    alt=""
                                    className="h-full w-full object-cover object-center"
                                />
                            </div>
                        </div>
                    </a>
                </div>
                <div className="menu-box mb-12">
                    {isLoading ? <LoadingComponent /> : (<BoxImage listBox={listBox} />)}
                </div>
            </div>
        </DefaultLayout>
    );
}

export default Home;
