import { Link } from 'react-router-dom';
import { Disclosure } from '@headlessui/react';
import { Bars3Icon, XMarkIcon } from '@heroicons/react/24/outline';


function Header({ home = false }) {
    const navigation = [
        { name: 'Trang ch', href: '/', current: home },
        { name: 'Gii thiu', href: '#', current: false },
        { name: 'Cch s dng ti vin dng lo', href: '#', current: false },
        { name: 'Cch s dng ti nh', href: '#', current: false },
        { name: 'Hnh nh xa', href: '#', current: false },
        { name: 'T liu xa', href: '#', current: false },
    ];

    function classNames(...classes) {
        return classes.filter(Boolean).join(' ');
    }

    return (
        <div className="sticky z-50 top-0 min-h-full mb-12">
            <Disclosure as="nav" className="bg-gray-800">
                {({ open }) => (
                    <>
                        <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8 sm:h-24 shadow-lg h-16 flex justify-between w-full">
                            <div className="flex items-center justify-between w-full">
                                <div className="flex-shrink-0 text-white">
                                    <Link to="/">LOGO</Link>
                                </div>
                                <div className="hidden md:block">
                                    <div className="ml-10 flex items-center space-x-4">
                                        {navigation.map((item) => (
                                            <Link
                                                key={item.name}
                                                to={item.href}
                                                className={classNames(
                                                    item.current
                                                        ? 'bg-orange-500 text-white'
                                                        : 'text-gray-300 hover:bg-gray-700 hover:text-white',
                                                    'rounded-md px-3 py-2 font-medium text-base text-center',
                                                )}
                                                aria-current={item.current ? 'page' : undefined}
                                            >
                                                {item.name}
                                            </Link>
                                        ))}
                                    </div>
                                </div>
                            </div>
                            <div className="-mr-2 flex md:hidden items-center justify-between">
                                {/* Mobile menu button */}
                                <Disclosure.Button className="relative bg-gray-800 p-3 text-gray-400 hover:bg-gray-700 hover:text-white">
                                    {open ? (
                                        <XMarkIcon className="block h-6 w-6" aria-hidden="true" />
                                    ) : (
                                        <Bars3Icon className="block h-6 w-6" aria-hidden="true" />
                                    )}
                                </Disclosure.Button>
                            </div>
                        </div>

                        <Disclosure.Panel className="md:hidden">
                            <div className="space-y-1 px-2 pb-3 pt-2 sm:px-3">
                                {navigation.map((item) => (
                                    <Link
                                        key={item.name}
                                        to={item.href}
                                        className={classNames(
                                            item.current
                                                ? 'bg-orange-500 text-white'
                                                : 'text-gray-300 hover:bg-gray-700 hover:text-white',
                                            'block rounded-md px-3 py-2 text-base font-medium',
                                        )}
                                    >
                                        {item.name}
                                    </Link>
                                ))}
                            </div>
                        </Disclosure.Panel>
                    </>
                )}
            </Disclosure>
        </div>
    );
}

export default Header;
